 <template>
  <table class="results-table__table">
    <tbody>
      <tr>
        <th>Name</th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'lap', 'active--desc': currentSortDir === 'desc' && currentSort === 'lap'}]"
            @click="EventBus.$emit('sortPitStopsAscDes', 'lap', false)"
          >Lap</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'stop', 'active--desc': currentSortDir === 'desc' && currentSort === 'stop'}]"
            @click="sortPitStopsAscDes('stop')"
          >No.</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'time', 'active--desc': currentSortDir === 'desc' && currentSort === 'time'}]"
            @click="sortPitStopsAscDes('time')"
          >Time</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'duration', 'active--desc': currentSortDir === 'desc' && currentSort === 'duration'}]"
            @click="sortPitStopsAscDes('duration')"
          >Duration</button>
        </th>
      </tr>
      <tr v-for="result in racePitStops" :key="result.index">
        <td class="results-table__name">{{ result.driverId | cleanName }}</td>
        <td class="right-align">{{ result.lap }}</td>
        <td class="right-align">{{ result.stop }}</td>
        <td class="right-align">{{ result.time }}</td>
        <td class="right-align">{{ result.duration }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'pit-table',
  props: ['racePitStops', 'currentSortDir', 'currentSort'],
  filters: {
    cleanName: function (value) {
      let val = value.substr(value.indexOf('_') + 1)
      val = val.charAt(0).toUpperCase() + val.substr(1)
      return val
    }
  },
  methods: {
    sortPitStopsAscDes: function (e) {
      EventBus.$emit('sortPitStopsAscDes', e, false)
    }
  }
}
</script>
