<template>
  <aside class="race-select">
    <div class="race-select__inner">
      <h2 class="heading">Race select</h2>
      <div>{{activeRace}}</div>
      <div class="race-select__year">
        <template v-for="(season, index) in seasons">
          <button :key="season[index]" ref="season">{{ season.season }}</button>
        </template>
        <div ref="raceSelectRaceName" v-if="!isFetching">
          <span
            v-for="(race, index) in seasons[activeSeason].Races"
            :key="race[index]"
          >{{ race.raceName }}</span>
        </div>
      </div>
      <!-- <form class="race-select">
        <select v-model="activeSeason" v-if="!isFetching">
          <option disabled value>Please select a year</option>
          <template v-for="(season, index) in seasons">
            <option :value="index" :key="season[index]" ref="season">{{ season.season}}</option>
          </template>
        </select>

        <select v-if="yearSelected" v-model="activeRace" ref="raceSelectRaceName">
          <option disabled value>Please select a race</option>
          <option
            v-for="(race, index) in seasons[activeSeason].Races"
            :value="index +1"
            :key="race[index]"
          >{{ race.raceName }}</option>
        </select>
      </form>-->
    </div>
  </aside>
</template>
<script>

export default {
  name: 'race-select',
  data: () => ({
    seasonCounter: 0,
    isFetching: true,
    seasons: [],
    activeSeason: '2019',
    activeRace: 'bahrain_grand_prix',
    yearSelected: false,
    baseUrl: process.env.VUE_APP_BASE_URL
  }),
  created () {
    // get season schedule for current year
    this.currentYear = new Date().getFullYear()
    this.currentYear = this.currentYear - 1 // Skip 2020 because of Covid-19
    fetch(`${this.baseUrl}data/seasons/${this.currentYear}.json`, { cache: 'force-cache', mode: 'same-origin' })
      // fetch(`assets/data/seasons/${this.currentYear}.json`, { cache: 'force-cache', mode: 'same-origin' })
      .then(response => {
        return response.json()
      })
      .then(data => {
        // Work with JSON data here
        this.seasons.push(data.MRData.RaceTable)
      })
      .catch(err => {
        // eslint-disable-next-line no-console
        console.error(`'${err}' happened!`); return {}
      })
  },
  mounted () {
    // get season schedule for all previous years
    const previousYear = this.currentYear - 1
    for (let year = previousYear; year >= 1950; year--) {
      fetch(`${this.baseUrl}data/seasons/${year}.json`, { cache: 'force-cache', mode: 'same-origin' })
        .then(response => {
          return response.json()
        })
        .then(data => {
          // Work with JSON data here
          this.seasons.push(data.MRData.RaceTable)
          this.seasonCounter++

          // Return if not last iteration
          if (year !== 1950) return
          this.getCurrentYearRaces()

          // Render component once promise fulfilled
          this.isFetching = false
        })
        .catch(err => {
          // eslint-disable-next-line no-console
          console.error(`'${err}' happened!`); return {}
        })
    }
  },
  methods: {
    getCurrentYearRaces () {
      // Get ,index of current active season
      const findYear = this.seasons.map(function (e) { return e.season }).indexOf(this.activeSeason)
      this.activeSeason = findYear
    },
    raceRouting: function (e) {
      const valSeason = this.$refs.season[this.activeSeason].innerHTML
      const valRound = this.activeRace
      const path = `/${valSeason}/${valRound}`

      // Don't run if same path
      if (this.$route.path !== path) {
        this.$router.push({
          name: 'otherRaces',
          params: {
            season: valSeason,
            round: valRound
          }
        })
      }
    }
  }
}
</script>
