 <template>
  <table class="results-table__table">
    <tbody>
      <tr>
        <th>Name</th>
        <th class="right-align">
          <button
            :class="['results-table__sortable ', {'active--asc': currentSortDir === 'asc' && currentSort === 'lap', 'active--desc': currentSortDir === 'desc' && currentSort === 'lap'}]"
            @click="sortResultsAscDes('lap')"
          >Lap</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'time', 'active--desc': currentSortDir === 'desc' && currentSort === 'time'}]"
            @click="sortResultsAscDes('time')"
          >Time</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'speed', 'active--desc': currentSortDir === 'desc' && currentSort === 'speed'}]"
            @click="sortResultsAscDes('speed')"
          >Top Speed</button>
        </th>
      </tr>
      <tr v-for="result in raceResults" :key="result.index">
        <td class="results-table__name">
          <span :data-driver="`${result.Driver.givenName} ${result.Driver.familyName}`">
            <span class="results-table__given-name">{{ result.Driver.givenName }}</span>
            {{ result.Driver.familyName }}
          </span>
        </td>
        <td class="right-align">{{ result.FastestLap.lap || ''}}</td>
        <td class="right-align">{{ result.FastestLap.Time.time || '' }}</td>
        <td
          class="right-align"
        >{{result.FastestLap.AverageSpeed.speed+result.FastestLap.AverageSpeed.units || '' }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'lap-table',
  props: ['raceResults', 'currentSortDir', 'currentSort'],
  methods: {
    sortResultsAscDes: function (e) {
      EventBus.$emit('sortResultsAscDes', e, false)
    }
  }
}
</script>
