<template>
  <div class="other-races page-wrapper">
    <RaceHeader :race="race" :raceDataIsFetching="raceDataIsFetching"></RaceHeader>
    <RaceSelect></RaceSelect>
    <template>
      <RaceInformation v-if="race && race.Circuit && race.Circuit.Location" :race="race"></RaceInformation>
    </template>
    <RaceResults :raceResults="raceResults" :racePitStops="racePitStops"></RaceResults>
    <template v-if="qualifyingResults">
      <QualifyingResults :qualifyingResults="qualifyingResults"></QualifyingResults>
    </template>
  </div>
</template>

<script>
import RaceHeader from '@/components/RaceHeader'
import RaceSelect from '@/components/RaceSelect'
import RaceInformation from '@/components/RaceInformation'
import RaceResults from '@/components/RaceResults'
import QualifyingResults from '@/components/QualifyingResults'
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'other-races',
  props: ['race', 'raceResults', 'racePitStops', 'qualifyingResults', 'raceDataIsFetching'],
  components: {
    RaceHeader,
    RaceSelect,
    RaceInformation,
    RaceResults,
    QualifyingResults
  },
  created () {
    const controller = new AbortController()

    this.options = { cache: 'force-cache', method: 'GET', signal: controller.signal }

    // Ergast data
    const season = this.$route.params.season
    const round = this.$route.params.round

    this.data = {
      results: `https://ergast.com/api/f1/${season}/${round}/results.json`,
      qualifying: `https://ergast.com/api/f1/${season}/${round}/qualifying.json`,
      pitStops: `https://ergast.com/api/f1/${season}/${round}/pitstops.json`
    }

    EventBus.$emit('raceRequest', this.data, this.options, controller)
  }
}
</script>
