<template>
  <table v-if="qualifyingResults.length" class="results-table__table" ref="defaultTable">
    <tbody>
      <tr>
        <th>P</th>
        <th class="hide-xs">No</th>
        <th colspan="2">Name</th>
        <th
          :class="['results-table__constructor', {'hide-xs': qualifyingResults[0].Q2 || qualifyingResults[0].Q3}]"
        >Team</th>
        <th class="right-align">
          <button
            v-if="qualifyingResults[0].Q1"
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q1', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q1'}]"
            @click="sortResultsAscDes('Q1')"
          >Q1</button>
        </th>
        <th class="right-align">
          <button
            v-if="qualifyingResults[0].Q2"
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q2', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q2'}]"
            @click="sortResultsAscDes('Q2')"
          >Q2</button>
        </th>
        <th class="right-align">
          <button
            v-if="qualifyingResults[0].Q3"
            :class="['results-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q3', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q3'}]"
            @click="sortResultsAscDes('Q3')"
          >Q3</button>
        </th>
      </tr>
      <tr v-for="(result, index) in qualifyingResults" :key="result.index">
        <td>{{ result.position }}</td>
        <td class="hide-xs results-table__no">{{ result.number }}</td>
        <td class="results-table__flag">
          <img :src="getFlagImgUrl(index)" alt />
          <span class="results-table__nationality">{{ result.Driver.nationality }}</span>
        </td>
        <td>
          <span class="results-table__given-name">{{ result.Driver.givenName }}</span>
          {{ result.Driver.familyName }}
        </td>
        <td
          :class="['results-table__constructor', {'hide-xs': qualifyingResults[0].Q2 || qualifyingResults[0].Q3}]"
        >
          <span>
            <span :data-constructor="result.Constructor.name">{{ result.Constructor.name }}</span>
          </span>
        </td>
        <td class="right-align" v-if="result.Q1">{{ result.Q1 }}</td>
        <template v-else>
          <td class="right-align" v-if="qualifyingResults[0].Q1"></td>
        </template>
        <td class="right-align" v-if="result.Q2">{{ result.Q2 }}</td>
        <template v-else>
          <td class="right-align" v-if="qualifyingResults[0].Q2"></td>
        </template>
        <td class="right-align" v-if="result.Q3">{{ result.Q3 }}</td>
        <template v-else>
          <td class="right-align" v-if="qualifyingResults[0].Q3"></td>
        </template>
      </tr>
    </tbody>
  </table>
</template>

<script>

export default {
  name: 'qualifying-table',
  props: ['qualifyingResults'],
  data: () => ({
    currentSort: 'Q3',
    currentSortDir: 'asc'
  }),
  methods: {
    sortResultsAscDes: function (s) {
      // if s == current sort
      if (s === this.currentSort) {
        this.currentSortDir = this.currentSortDir === 'asc' ? 'desc' : 'asc'
      }
      this.currentSort = s
      this.sortResults(this.currentSort)
    },
    sortResults: function (e) {
      this.qualifyingResults.sort((a, b) => {
        let modifier = 1
        if (this.currentSortDir === 'desc') modifier = -1
        // Ignore if property is empty
        if (!a[e] || !b[e]) return false
        if (a[e] < b[e]) return -1 * modifier
        if (a[e] > b[e]) return 1 * modifier
      })
    },
    getFlagImgUrl (i) {
      const fallback = require.context('@/assets/img/', false, /\.png$/)
      const flagImages = require.context('@/assets/img/flags/', false, /\.svg$/)
      let flagImage = ''
      try {
        flagImage = flagImages(`./${this.qualifyingResults[i].Driver.nationality}.svg`)
      } catch (error) {
        flagImage = fallback('./fallback.png')
      }
      return flagImage
    }
  }
}
</script>
