<template>
  <table class="results-table__table">
    <tbody>
      <tr>
        <th>
          <button
            :class="['results-table__sortable results-table__position', {'active--asc': currentSortDir === 'asc' && currentSort === 'position', 'active--desc': currentSortDir === 'desc' && currentSort === 'position'}]"
            @click="sortResultsAscDes('position')"
          >P</button>
        </th>
        <th>Name</th>
        <th>Team</th>
        <th class="right-align">
          <button
            :class="['results-table__sortable results-table__grid', {'active--asc': currentSortDir === 'asc' && currentSort === 'grid', 'active--desc': currentSortDir === 'desc' && currentSort === 'grid'}]"
            @click="sortResultsAscDes('grid')"
          >Grid</button>
        </th>
        <th class="right-align">
          <button
            :class="['results-table__sortable results-table__grid-diff', {'active--desc': currentSortDir === 'asc' && currentSort === 'gridDiff', 'active--asc': currentSortDir === 'desc' && currentSort === 'gridDiff'}]"
            @click="sortResultsAscDes('gridDiff')"
          >+/-</button>
        </th>
      </tr>
      <tr v-for="result in raceResults" :key="result.index">
        <td>{{ result.positionText }}</td>
        <td>
          <span class="results-table__given-name">{{ result.Driver.givenName }}</span>
          {{ result.Driver.familyName }}
        </td>
        <td class="results-table__constructor">
          <span :data-constructor="result.Constructor.name">{{ result.Constructor.name }}</span>
        </td>
        <td class="right-align" v-if="result.grid">{{ result.grid }}</td>
        <td
          :class="[{'results-table__grid-diff--neg ': gridChangeNeg(result.gridDiff)}, {'results-table__grid-diff--pos': gridChangePos(result.gridDiff)}, 'results-table__grid-diff right-align']"
        >{{ result.gridDiff}}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'position-table',
  props: ['raceResults', 'currentSortDir', 'currentSort'],
  methods: {
    sortResultsAscDes: function (e) {
      EventBus.$emit('sortResultsAscDes', e, false)
    },
    gridChangeNeg (e) {
      if (e < 0) return true
    },
    gridChangePos (e) {
      if (e > 0) return true
    }
  }
}
</script>
